<template>
    <div class="search-car importcar">
        <v-header :type = 2>平行进口</v-header>
        <v-panel  v-if="brandArr.length" :title="panelTitle">
             <a  class="brand-item" v-for="(item,index) in brandArr" :key="index" @click="brandClick(item.id)">
                 <div class="img">
                     <img :src="item.brandIcon" alt="">
                 </div>                
                 <p>{{item.brand}}</p>
             </a>
             <a @click="brandList" class="brand-item all">
                 <div class="img">
                    <i class="icon-more "></i>
                 </div>
                 <p>全部品牌</p>
             </a>
         </v-panel>
         <scroll :data="carArr" @scroll="scroll" :listen-scroll="listenScroll" :probe-type="probeType" :pullup="isShow"  :pulldown="isShow" @pulldown="pulldown"  @scrollToEnd="pullup"  class="car-list" ref="carList">
             <div class="list-wraper">
                  <v-carList :carArr="carArr" :car-type ='2'></v-carList>
             </div>           
             <div v-show="!carArr.length" class="loading-container">
                <loading></loading>
            </div>
         </scroll>
        <router-view></router-view>
    </div>
</template>

<script>
import vHeader from '@/components/mobile/m-header'
import vCarList from '@/components/mobile/m-carList'
import Scroll from '@/components/base/scroll/scroll'
import Loading from '@/components/base/loading/loading'
import vPanel from '@/components/mobile/m-panel'
import {MOBILE_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return {
           carArr:[],
           panelTitle:"品牌查找",
           brandArr:[],
           scrollY: 0,
           isShow: true,  
           page:1,
           brandId:null
        }
    },
    created() {
      this.probeType = 3
      this.listenScroll = true
    },
    components:{
        vHeader,
        vCarList,
        Scroll,
        vPanel,
        Loading
    },
    methods:{
         brandList:function(){
            this.$router.push({
                path:'/home/importCarList/brandList'
            })
        },
        pulldown(){  
            this.carArr = [];
            this.page = 1;
            //下拉刷新重新初始化             
            this.$refs.carList.$emit('pullrefresh.finishLoad');  
            this.createJson();
        },  
        pullup(){  
            if(!this.page) return  
            this.page ++   
            //上拉加载重新初始化  
            this.$refs.carList.$emit('infinitescroll.reInit');
            this.createJson(this.page);  
        },  
        getOptions(data){
            this.selectOptions = data;
            this.carArr = [];
            this.createJson();
        },
        getBrandArr(data){           
            let _this = this;
            $.ajax({
                url:MOBILE_COMMONURL+"/queryImportCarBrandList",
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    let brandArr = [];
                    if(data.code == SUCCESS_CODE){
                        for(let key in data.data.brandList){
                             brandArr = [...brandArr, ...data.data.brandList[key]] 
                        }
                    }
                    _this.brandArr = brandArr.slice(0,9);
                }
            })
        },
        brandClick(id){
            this.brandId = id;
            this.createJson();
        },
        createJson(page){
            let json = {};
            json.pageSize = 10;
            if(page){
                json.pageNum = page;
            }else{
                json.pageNum = 1
            }
            if(this.brandId != null){                    
                json.brandId = this.brandId;            
            }else{
                json.brandId=null;
            }
            this.getcarData(json);
        },
        getcarData:function(data){
            let _this = this;
            $.ajax({
                url:MOBILE_COMMONURL+"/queryImportCarList",
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        if(data.data.importCarList.length > 0){  
                            _this.carArr = [..._this.carArr, ...data.data.importCarList]  
                            //单次请求数据加载完毕后  
                            _this.$refs.carList.$emit('infinitescroll.finishLoad');  
                        }else{  
                            //把page设置成false 数据已经加载完毕了 不用在加载了  
                            _this.page = false  
                            //所有数据加载完毕后  
                            _this.$refs.carList.$emit('infinitescroll.loadedDone');  
                        }
                    }
                }
            })
        },
        scroll(pos) {
            this.scrollY = pos.y;
            console.log(pos.y);
        },
    },
    mounted(){
        this.createJson();
        this.getBrandArr();
        this.$refs.carList.$el.style.top = `230px`;       
    }
}
</script>



